.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary));
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  background: linear-gradient(45deg, transparent, var(--color-bg));
}

.about__me-image:hover {
  transform: rotate(0deg);
  transition: var(--transition);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about_card {
  background: var(--color-primary);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 0.8rem;
  text-align: center;
  transition: var(--transition);
}

.about_card:hover {
  background: transparent;
  border-color: red;
  cursor: default;
}

.about__icon {
  color: red;
  font-size: 1.4rem;
  margin-bottom: 0.8rem;
}

.about_card h5 {
  font-size: 0.9rem;
}

.about_card small {
  font-size: 0.7rem;
}

.about__content p {
  margin: 1rem 0;
  font-size: 0.8rem;
}
h2{
  color: white;
}

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
   
  }
  .btn{
    margin-bottom: 1rem;
  }

  .about__me {
    width: 70%;
    margin: 2rem auto 4rem;
  }
  .about__content p {
    margin: 1rem 0 1.5rem;
  }
  .about__content {
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .about__me {
    width: 80%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about__content {
    text-align: center;
  }
  .about__content p {
    margin: 1.5rem 0;
  }
}
